import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import {
  getSalaryFrequencyConfig,
  getUser,
  deleteSalaryFrequencyConfig,
  downloadFile,
} from 'services/salaryFrequencyConfigService';
import Btn from 'components/common/button/Btn';
import { getQueryParams } from 'utils/urlUtil';
// import { dateToString } from 'utils/formatUtil';
import FormSearch from './FormSearch';

class DocOutList extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
    getUser()
      .then(res => {
        this.setState({
          token: res.data.accessToken,
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  dataFormSeach = data => {
    this.setState({
      paramSearch: data,
      reload: this.state.reload + 1,
    });
  };

  handleDelete = async id => {
    const { token } = this.state;
    await deleteSalaryFrequencyConfig(id, token);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportDocOut = () => {
    const { paramSearch, token } = this.state;
    downloadFile(paramSearch, token);
  };

  render() {
    const { reload, paramSearch } = this.state;
    const columns = [
      {
        title: 'Vị trí',
        dataIndex: 'positionName',
        key: 'positionName',
      },
      {
        title: 'Số năm định kỳ tăng lương',
        dataIndex: 'frequencyYear',
        key: 'frequencyYear',
      },
      {
        title: 'Hệ số tăng định kỳ',
        dataIndex: 'frequencyPoint',
        key: 'frequencyPoint',
      },
      {
        title: 'Hệ số khởi điểm',
        dataIndex: 'firstLevelPoint',
        key: 'firstLevelPoint',
      },
      {
        title: 'Số bậc tối đa',
        dataIndex: 'maxLevel',
        key: 'maxLevel',
      },

      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/SalaryFrequencyConfig/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Nhập kỷ luật">
        <Row>
          <Col span={24}>
            <FormSearch dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/SalaryFrequencyConfig/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getSalaryFrequencyConfig}
              paramSearch={paramSearch}
              columns={columns}
              rowKey="id"
            />
          </Col>
          <Col style={{ textAlign: 'center' }} span={24}>
            <Btn onClick={this.exportDocOut} icon="download">
              Download
            </Btn>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DocOutList />
  </Page>
);



